$blue: #7dbcd2;
$darkBlue: #0b8aad;
$white : #FFF;
$darkGrey: #b9b9b9;
$lightGrey: #e7e7e7;
$smallText: 12px;
$mediumText: 14px;

* {
  font-size: $mediumText;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}


.pair-device-button {
  margin-top: -5vh;
  position: absolute;
  cursor: pointer;
  text-align: center;
  padding: 10px 10px;
  font-size: 44px;
  font-weight: bold;
  color: $darkBlue;
  border: 2px solid $darkBlue;
  border-radius: 10px;
}

.pair-device-button:hover {
  background-color: $blue;
  color: $white;
  border: none;
}

.settings-popup {
  width: 300px;
  padding: 29px;
  border: 1px solid $blue;
  border-radius: 58px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: white;
  box-shadow: 5px 5px #80808029;
  position: absolute;
  right: 6vw;
  top: 15px;
}


.button {
  cursor: pointer;
  padding: 10px 20px;
  background-color: $darkBlue;
  color: white;
  border-radius: 15px;
  margin-top: 15px;
  font-size: 22px
}

.button:hover {
  background-color: $blue;
}

.option {
  font-size: 18px;
  margin-bottom: 10px;
  font-weight: bold;
  width: 59%;
}

.load-csv {
  width: 55%;
  font-size: 25px;
}
